body {
    margin: 0;
}

.content {
    padding-top: 10pt;
    margin-left: 240pt;
    margin-right: 40px;
    max-width: 1000px;
    font-size: 14pt;
}

.content h1 {
    text-align: center;
    font-size: 23pt;
}

.content h2 {
    text-align: center;
    font-size: 18pt;
    margin-top: 20pt;
}

.content h3 {
    text-align: center;
    font-size: 15pt;
    text-decoration: none;
    margin-bottom: 0;
}

.content h4 {
    text-align: center;
    font-size: 15pt;
    text-decoration: none;
    font-weight: normal;
    font-style: italic;
    margin: 5px;
    margin-top: 0;
}

.content ul li {
    padding-top: 5pt;
}

.content p a {
    text-decoration: none;
}

.content .narrow {
    width: 75%;
    margin: auto;
}

ol {
    margin-left: 10%;
}

.nav ul {
    list-style-type: none;
    background-color: #f1f1f1;
    position: fixed;
    margin: 0;
    padding: 0;
    height: 100%;
    width: inherit; /* Inherit from containing element */
    overflow: auto; /* Allow scrollbar automatically */
}

/* Inactive page */
.nav li a {
    display: block;
    color: #000;
    padding: 8px 16px;
    text-decoration: none;
}

/* Active page */
.nav li a.active {
    background-color: #4CAF50;
    color: white;
}

.nav li a:hover:not(.active) {
    background-color: #555;
    color: white;
}

.img-left {
    float: left;
    margin-right: 20px;
    margin-left: 0;
    margin-top: 5px;
    margin-bottom: 5px;
}

.img-right {
    float: right;
    margin-right: 0;
    margin-left: 20px;
    margin-top: 5px;
    margin-bottom: 5px;
}

.img-ctr {
    display: block;
    margin: 0px auto;
}

.aside-right {
    float: right;
    margin-right: 0;
    margin-left: 20px;
    margin-top: 5px;
    margin-bottom: 5px;
    width: 20%;
    outline: 2px solid black;
    background-color: #f1f1f1;
    padding: 20px;
    font-size: 13pt;

}

.aside-left {
    float: left;
    margin-right: 20px;
    margin-left: 0;
    margin-top: 5px;
    margin-bottom: 5px;
    width: 20%;
    outline: 2px solid black;
    background-color: #f1f1f1;
    padding: 20px;
    font-size: 13pt;

}

.code {
    width: inherit;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 10%;
    margin: auto;
    font-family: 'Roboto Mono', monospace;
    font-size: 15pt;
}

.content .big-list {
    margin: auto;
    margin-top: -20pt;
    width: 70%;
}

.content .big-list li {
    padding-top: 25px;
}

.content .directory {
    text-align: center;
    list-style-type: none;
    padding: 0;
    margin: 0
}

.content .video {
    width: 100%;
    height: 600px;
}

#pro-tip {
    width: 50%;
    margin: auto;
    padding: 0;
    text-align: center;
    outline: solid;
}

#nav_home {
    margin: 0;
    padding: 0;
    width: 200pt;
    overflow: auto;
}

#nav_bar_1 {
    margin: 0;
    padding: 0;
    width: 100pt;
    height: 100%;
    overflow: auto;
}

#nav_bar_2 {
    margin-left: 100pt;
    padding: 0;
    width: 100pt;
    height: 100%;
    overflow: auto;
}

#introduction {
    padding-top: 10px;
}

#introduction h1 {
    margin-bottom: 5px;
    margin-top: 25px;
}

#references ol {
    margin-left: 0;
}

#credits {
    text-align: center;
    padding-bottom: 25px;
}
